*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

$resolutions: (
  'sm': 576,
  'md': 768,
  'lg': 992,
  'xl': 1074,
  'xxl': 1500,
);

@each $resolution, $i in $resolutions {
  .container-max-w-#{$resolution} {
    max-width: #{$i}px;
    margin: 0 auto !important;
  }
}

ul {
  list-style: none;
  padding-inline: unset;
}

a {
  color: inherit;
  text-decoration: none;
}

.radius {
  &-5 {
    border-radius: 5px;
  }

  &-8 {
    border-radius: 8px;
  }
}
